.card-set {
  gap: var(--ident);
  flex-wrap: wrap;
  display: flex;
}

.items-set {
  flex-basis: calc((100% - var(--ident) * (var(--items)  - 1)) / var(--items));
}

.btn {
  border: 1px solid var(--main-color);
  color: var(--primary-text-color);
  cursor: pointer;
  background: none;
  border-radius: 15px;
  padding: 14px 28px;
  font-size: 14px;
  font-weight: 500;
}

.btn-dr-theme {
  color: var(--light-text-color);
}

.btn:hover {
  background-color: var(--main-color);
  color: var(--light-text-color);
  transition: all .3s;
}



